import CommentList from "./comment-list";
import CommentEditor from "@/components/base/comment-editor/comment-editor";
import cloneDeep from "lodash/cloneDeep";

export default {
    components: {
        CommentEditor,
        CommentList
    },

    props: {
        total: {
            type: Number,
            default: 0
        },
        comments: {
            type: Array,
            default: () => []
        },

        articleId: {
            type: Number
        },

        loading: {
            type: Boolean,
            default: false
        }
    },

    data() {
        return {
            isShowReplyContent: false,
            parentId: 0,
            page: 0
        };
    },

    methods: {
        onLoadMore() {
            if (this.loading) {
                return
            }
            this.page++
            this.$store.dispatch('article/getMoreComments', {
                page: this.page,
                count: 10
            })
        },
        onReply(comment) {
            this.parentId = comment.id
            this.$refs.editor.reply = {
                nickname: comment.nickname,
                content: comment.content
            }
            this.isShowReplyContent = true;
        },

        closeReply() {
            this.isShowReplyContent = false
            this.parentId = 0
            this.$refs.editor.reply = {
                nickname: '',
                content: ''
            }
            this.$refs.editor.resetField()
        },
        handleLocalInfo(k,v){
            const localInfo=this.$cookies.get(k) || []
            if(v===undefined){
                return this.$cookies.get(k) || []
            }else {
                if(!localInfo.includes(v)){
                    localInfo.push(v)
                    this.$cookies.set(k,localInfo,{
                        path:'/',
                        maxAge: 60 * 60 * 24 * 365 * 3
                    })
                }
            }
        },
        async onSend(data) {
            if (!this.articleId) {
                return
            }
            if (this.isShowReplyContent) {
                // 回复评论
                if (!this.parentId) {
                    return
                }
                try {
                    data.classId = this.articleId
                    data.parentId = this.parentId
                    const res = await this.$store.dispatch('article/replyComment', data)
                    if (res.code === 1) {
                        this.closeReply()
                        this.handleLocalInfo('commentIds',String(this.articleId))
                        this.$emit('createCommentSuccess')
                    }
                } catch (e) {
                    // eslint-disable-next-line no-console
                    console.log(e)
                }
            } else {
                // 创建评论
                try {
                    data.classId = this.articleId
                    data.parentId = this.parentId
                    const res = await this.$store.dispatch('article/createComment', data)
                    if (res.code === 1) {
                        this.$refs.editor.resetField()
                        this.handleLocalInfo('commentIds',String(this.articleId))
                        this.$emit('createCommentSuccess')
                    }
                } catch (e) {
                    // eslint-disable-next-line no-console
                    console.log(e,'===')
                }
            }
        }
    }
};
