import ClickOutside from '@/services/directives/click-outside'

export default {
  props: {
    loading: {
      type: Boolean,
      default: false
    },

    imgSrc: {
      type: String,
      default: ''
    },

    visible: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      tmp: {
        overflow: ''
      }
    }
  },

  directives: {
    ClickOutside
  },

  computed: {
    dialogVisible() {
      if (this.visible) {
        this.lock()
      } else {
        this.unlock()
      }
      return this.visible
    }
  },

  methods: {
    lock() {
      if (process.client) {
        this.tmp.overflow = document.body.style.overflow
        document.body.style.overflow = 'hidden'
      }
    },

    unlock() {
      if (process.client) {
        document.body.style.overflow = this.tmp.overflow
      }
    },

    close() {
      this.unlock()
      this.$emit('update:visible', false)
    }
  }
}
